export function MoveElementOnScroll(
  element,
  {
    container,
    min = 0,
    max = element.offsetHeight,
    speed = 1,
    smooth = 2,
    onUpdate = () => null,
    onEndScroll = () => null,
  },
) {
  let moving = false;
  let pos = min;
  let lastPos = pos;

  // todo: move this to utils if we finally need it
  const requestFrame = (function() {
    return (
      window.requestAnimationFrame ||
      window.webkitRequestAnimationFrame ||
      window.mozRequestAnimationFrame ||
      window.oRequestAnimationFrame ||
      window.msRequestAnimationFrame ||
      function(func) {
        window.setTimeout(func, 1000 / 50);
      }
    );
  })();

  function update() {
    moving = true;
    const delta = (pos - lastPos) / smooth;
    lastPos += delta;

    if (Math.abs(delta) > 0.7) {
      requestFrame(() => {
        onUpdate(lastPos, delta);
        update();
      });
    } else {
      onEndScroll(pos);
      moving = false;
    }
  }

  function scrolling(e) {
    e.preventDefault(); // disable default scrolling
    let delta = e.delta || e.wheelDelta;
    if (delta === undefined) {
      // for firefox
      delta = -e.detail;
      delta *= 10 * speed;
    }

    pos += -delta * speed;
    pos = Math.max(min, Math.min(pos, max)); // limit scrolling
    if (!moving) {
      update();
    }
  }

  container.addEventListener('mousewheel', scrolling, false);
  container.addEventListener('DOMMouseScroll', scrolling, false);

  return {
    unsubscribe: () => {
      container.removeEventListener('mousewheel', scrolling);
      container.removeEventListener('DOMMouseScroll', scrolling);
    },
    updatePosition: newPos => {
      pos = -newPos;
      lastPos = -pos;
    },
  };
}

export default MoveElementOnScroll;
