import React, { useRef, useMemo } from 'react';
import PropTypes from 'prop-types';
import { getVisibleTimeByFormat } from '@elliemae/ds-utilities/timeUtils';
import memoizeOne from 'memoize-one';
import { aggregatedClasses } from '@elliemae/ds-classnames';
import TimeInputs from './TimeInputs';

const blockName = 'input-time';

const InputTimeContainer = aggregatedClasses('div')(blockName);

const TimeInputImpl = ({
  format,
  innerRef,
  onChange,
  className,
  value,
  disabled,
  clearable,
  'arial-label': ariaLabel,
}) => {
  const getVisibleTimeInputs = memoizeOne((formatValue) =>
    getVisibleTimeByFormat(formatValue),
  );
  let inputsContainer = useRef();

  const handleFocus = (e) => {
    if (!e.target.type) {
      inputsContainer.querySelector('input').focus();
    }
  };

  const visibleInputs = useMemo(() => getVisibleTimeInputs(format));

  return (
    <InputTimeContainer
      className={className}
      disabled={disabled}
      innerRef={innerRef}
      onClick={handleFocus}
    >
      <TimeInputs
        aria-label={ariaLabel}
        clearable={clearable}
        disabled={disabled}
        format={format}
        innerRef={(node) => {
          inputsContainer = node;
        }}
        onChange={onChange}
        time={value}
        {...visibleInputs}
      />
    </InputTimeContainer>
  );
};

TimeInputImpl.propTypes = {
  containerProps: PropTypes.shape({}),
  /**
   * Inner reference to the input
   */
  innerRef: PropTypes.any,
  /**
   * Classes to add to the container
   */
  className: PropTypes.string,
  /**
   * Styles to add to the container
   */
  style: PropTypes.shape({}),
  /**
   * Allows a function that is triggered once the time input changes
   */
  onChange: PropTypes.func,
  /**
   * Valid time format
   */
  format: PropTypes.string,
  /**
   * Default value once the component is initialized
   */
  value: PropTypes.string,
  /**
   * Whether the time input is clearable or not
   */
  clearable: PropTypes.bool,
  /**
   * Whether the time input is disabled or not
   */
  disabled: PropTypes.bool,
  /**
   * Aria label to add to the container
   */
  'aria-label': PropTypes.bool,
  'arial-label': PropTypes.bool,
};

export default TimeInputImpl;
