import { addLeadingZeros } from '@elliemae/ds-utilities/timeUtils';
import { parseInt, isNaN } from '@elliemae/ds-utilities/utils';
import {
  AM,
  PM,
  ARROW_UP,
  ARROW_DOWN,
  SHIFT,
  PLACEHOLDER_TIME,
} from '../../../../shared-configs/constants';

const isAM = (time) => time && time.hour() < 12;

export const isArrowIncrementDecrement = (key) =>
  key === ARROW_DOWN || key === ARROW_UP || key === SHIFT;

export const setNativeValue = (element, value) => {
  if (!Object.getOwnPropertyDescriptor(element, 'value')) return;
  const valueSetter = Object.getOwnPropertyDescriptor(element, 'value').set;
  const prototype = Object.getPrototypeOf(element);
  const prototypeValueSetter = Object.getOwnPropertyDescriptor(
    prototype,
    'value',
  ).set;

  if (valueSetter && valueSetter !== prototypeValueSetter) {
    prototypeValueSetter.call(element, value);
  } else {
    valueSetter.call(element, value);
  }
  element.dispatchEvent(new Event('input', { bubbles: true }));
};

export const placeholderFormat = (position, format, use12Hours) => {
  if (position === PLACEHOLDER_TIME.hour) {
    return _placeholderHour(format, use12Hours);
  }
  if (position === PLACEHOLDER_TIME.minutes) {
    return format.indexOf('mm') > -1 ? 'mm' : 'm';
  }
  if (position === PLACEHOLDER_TIME.seconds) {
    return format.indexOf('ss') > -1 ? 'ss' : 's';
  }
  if (position === PLACEHOLDER_TIME.meridiem) {
    return format.indexOf('A') > -1 ? 'A' : 'a';
  }
};

const _placeholderHour = (format, use12Hours) => {
  if (use12Hours) {
    return format.indexOf('hh') > -1 ? 'hh' : 'h';
  }
  return format.indexOf('HH') > -1 ? 'HH' : 'H';
};

const formatTimeNumber = (value, shouldAddLeadingZeros) => {
  if (value === null || value === undefined) return '';
  return shouldAddLeadingZeros ? addLeadingZeros(2)(value) : String(value);
};

export const formatMinutes = (format, value) => {
  const shouldAddLeadingZeros = format.indexOf('mm') > -1;
  return formatTimeNumber(value, shouldAddLeadingZeros);
};
export const formatSeconds = (format, value) => {
  const shouldAddLeadingZeros = format.indexOf('ss') > -1;
  return formatTimeNumber(value, shouldAddLeadingZeros);
};
export const formatHour = (format, value) => {
  const shouldAddLeadingZeros =
    format.indexOf('HH') > -1 || format.indexOf('hh') > -1;
  return formatTimeNumber(value, shouldAddLeadingZeros);
};

export const formatMeridiem = (format, value) =>
  format.indexOf('A') > -1 ? value.toUpperCase() : value.toLowerCase();

export const parseTimeNumberFromText = (stringValue) => {
  const parsedInt = parseInt(stringValue);
  if (isNaN(parsedInt)) return null;

  return parsedInt;
};

export const focusNextInput = (currentEl) => {
  const nextElement = currentEl.nextElementSibling;
  if (nextElement && nextElement.nextElementSibling) {
    nextElement.nextElementSibling.focus();
  }
};

export const focusPreviousInput = (currentEl) => {
  const previousElement = currentEl.previousElementSibling;
  if (previousElement && previousElement.previousElementSibling) {
    previousElement.previousElementSibling.focus();
  }
};

export const getValidTimeNumber = ({ min, max }, number, typed) => {
  if (number === undefined || number === null) return null;
  if (number > max) {
    if (isNaN(typed)) return parseInt(typed);
    return max;
  }
  if (number < min) return min;
  return number;
};

export const shouldFocusNextInput = (max, number = 0, stringValue) => {
  const safeString = String(stringValue);
  const cleanString = safeString.startsWith('00')
    ? safeString.slice(1)
    : safeString;
  return number * 10 > max || String(max).length === cleanString.length;
};

export const getTimeValuesFromTime = (time, format, use12Hours) => {
  if (!time || !time.hour) return {};
  const hours = use12Hours ? time.hour() % 12 || 12 : time.hour();
  const minutes = time.minutes();
  const seconds = time.seconds();
  const meridiem = !isAM(time) ? PM : AM;

  return {
    hours: formatHour(format, hours),
    minutes: formatMinutes(format, minutes),
    seconds: formatSeconds(format, seconds),
    meridiem,
  };
};

export const resetTimeValues = () => ({
  hours: '',
  minutes: '',
  seconds: '',
  meridiem: AM,
});

export const commonInputProps = (onKeyDown, onInputFocus, onClick) => ({
  pattern: '[0-9]*',
  type: 'text',
  onKeyDown,
  onClick,
  onFocus: onInputFocus,
});
