/* eslint-disable complexity */
/* eslint-disable react/prop-types */
/* eslint-disable max-lines */
/* eslint-disable max-statements */
import React, { useEffect, useState } from 'react';
import { aggregatedClasses } from '@elliemae/ds-classnames';
import moment from 'moment';
import {
  commonInputProps,
  formatHour,
  formatMinutes,
  formatSeconds,
  formatMeridiem,
  getValidTimeNumber,
  setNativeValue,
  parseTimeNumberFromText,
  focusNextInput,
  focusPreviousInput,
  getTimeValuesFromTime,
  isArrowIncrementDecrement,
  shouldFocusNextInput,
  placeholderFormat,
} from './utils';
import {
  AM,
  PM,
  ARROW_UP,
  ARROW_DOWN,
  BACKSPACE,
  ESCAPE,
  HOME,
  HOUR_RANGE_12,
  HOUR_RANGE_24,
  GENERAL_TIME_RANGE,
  END,
  PLACEHOLDER_TIME,
  SHIFT,
} from '../../../../shared-configs/constants';

const blockName = 'input-times-group';

const InputTimesGroup = aggregatedClasses('div')(blockName);
const TimeInput = aggregatedClasses('input')(blockName, 'input');
const Divider = aggregatedClasses('span')(
  blockName,
  'divider',
  ({ value }) => ({
    value,
  }),
);

// eslint-disable-next-line react/prop-types
const TimeDivider = ({ content = ':', value = '' }) => (
  <Divider value={!!value}>{content}</Divider>
);

const TimeInputs = ({
  time,
  format,
  use12Hours,
  step = 1,
  showHours,
  showMinutes,
  showSeconds,
  onChange,
  disabled,
  innerRef,
  clearable,
  'arial-label': ariaLabel,
  'aria-label': ariaLabel2,
}) => {
  const [meridiem, setMeridiem] = useState('');
  const [hours, setHours] = useState('');
  const [minutes, setMinutes] = useState('');
  const [hasInputValue, setHasInputValue] = useState(false);
  const [seconds, setSeconds] = useState('');
  const [currentKey, setCurrentKey] = useState('');
  const [hasInputBeenTriggered, setHasInputBeenTriggered] = useState(false);

  const [lastTypedNumber, setLastTypedNumber] = useState(null);
  const inputRefs = {
    hours: null,
    minutes: null,
    seconds: null,
    meridiem: null,
  };

  useEffect(() => {
    if (time && moment(time).isValid()) {
      const timeValues = getTimeValuesFromTime(time, format, use12Hours);

      setMeridiem(timeValues.meridiem);
      setHours(timeValues.hours);
      setMinutes(timeValues.minutes);
      setSeconds(timeValues.seconds);
    } else {
      setMeridiem('');
      setHours('');
      setMinutes('');
      setSeconds('');
    }
  }, [time, format, use12Hours]);

  useEffect(() => {
    const selectedTime = time || moment();
    if (isTimeCompletelySet() && selectedTime.hours && hasInputBeenTriggered) {
      if (showHours && hours) selectedTime.hours(hours);
      if (showMinutes && minutes) selectedTime.minutes(minutes);
      if (showSeconds && seconds) selectedTime.seconds(seconds);
      if (use12Hours && meridiem) {
        const nextHours =
          meridiem.toLowerCase() === AM ? hours % 12 : (hours % 12) + 12;
        selectedTime.hour(nextHours);
      }
      onChange(selectedTime);
      setHasInputBeenTriggered(false);
    }
  }, [hours, minutes, seconds, meridiem]);

  const focusNextInputIfNeeded = (currentEl, max, number) => {
    if (
      shouldFocusNextInput(max, number, currentEl.value) &&
      !isArrowIncrementDecrement(currentKey) &&
      !hasInputValue
    ) {
      focusNextInput(currentEl);
    }
  };

  const getNextTimeValue = (value, incrementing = true) => {
    const parsedValue = parseTimeNumberFromText(value);
    return incrementing ? parsedValue + step : parsedValue - step;
  };

  const onInputFocus = (e) => {
    e.preventDefault();
    e.stopPropagation();
    e.target.select();
  };

  const onClick = (e) => {
    if (e.target.value) {
      setHasInputValue(true);
    }
  };

  // eslint-disable-next-line max-statements
  const onKeyDown = (event, max) => {
    const { target } = event;
    const { value } = target;
    setLastTypedNumber(event.key);
    switch (event.key) {
      case ARROW_UP: {
        event.preventDefault();
        setCurrentKey(event.key);
        const incrementedValue = getNextTimeValue(value);
        setNativeValue(event.target, incrementedValue);
        break;
      }
      case ARROW_DOWN: {
        event.preventDefault();
        setCurrentKey(event.key);
        const decrementedValue = getNextTimeValue(value, false);
        setNativeValue(event.target, decrementedValue);
        break;
      }
      case BACKSPACE: {
        event.preventDefault();
        if (!value) {
          focusPreviousInput(event.target);
        } else {
          setNativeValue(event.target, '');
        }
        break;
      }
      case ESCAPE: {
        event.preventDefault();
        event.target.blur();
        break;
      }
      case HOME: {
        event.preventDefault();
        setNativeValue(event.target, String(max));
        focusNextInput(event.target);
        break;
      }
      case END: {
        event.preventDefault();
        setNativeValue(event.target, '00');
        focusNextInput(event.target);
        break;
      }
      case SHIFT:
        event.preventDefault();
        setCurrentKey(event.key);
        break;
      default:
        break;
    }
  };

  const isTimeCompletelySet = () => {
    let neededValues = [];

    if (showHours) neededValues = [...neededValues, hours || null];
    if (showMinutes) neededValues = [...neededValues, minutes || null];
    if (showSeconds) neededValues = [...neededValues, seconds || null];
    if (use12Hours) neededValues = [...neededValues, meridiem || null];

    return !neededValues.some((value) => !value);
  };

  const handleSecondsChange = (e) => {
    const { value } = e.target;
    const secondsHandled = parseTimeNumberFromText(value);

    const nextSeconds = getValidTimeNumber(
      GENERAL_TIME_RANGE,
      secondsHandled,
      lastTypedNumber,
    );

    setSeconds(formatSeconds(format, nextSeconds));
    setHasInputBeenTriggered(true);
    focusNextInputIfNeeded(e.target, GENERAL_TIME_RANGE.max, value);
  };

  const handleMinutesChange = (e) => {
    const { value } = e.target;
    const minutesHandled = parseTimeNumberFromText(value);
    const nextMinutes = getValidTimeNumber(
      GENERAL_TIME_RANGE,
      minutesHandled,
      lastTypedNumber,
    );

    setMinutes(formatMinutes(format, nextMinutes));
    setHasInputBeenTriggered(true);
    focusNextInputIfNeeded(e.target, GENERAL_TIME_RANGE.max, value);
  };

  const handleHoursChange = (e) => {
    const { value } = e.target;
    const hour = parseTimeNumberFromText(value);
    const hoursRange = use12Hours ? HOUR_RANGE_12 : HOUR_RANGE_24;

    const nextHour = getValidTimeNumber(hoursRange, hour, lastTypedNumber);
    setHours(formatHour(format, nextHour));
    setHasInputBeenTriggered(true);
    focusNextInputIfNeeded(e.target, hoursRange.max, value);
  };

  const handleMeridiemKeyDown = (e) => {
    if (e.key === ARROW_UP || e.key === 'a') {
      setMeridiem(formatMeridiem(format, AM));
    } else if (e.key === ARROW_DOWN || e.key === 'p') {
      setMeridiem(formatMeridiem(format, PM));
    } else if (e.key === SHIFT) {
      setCurrentKey(e.key);
    } else if (e.key === BACKSPACE) {
      setMeridiem('');
    }
    setHasInputBeenTriggered(true);
  };

  const handleMeridiemBlur = (e) => {
    const { value } = e.target;
    if (value !== AM && value !== PM) {
      setMeridiem('');
    }
  };

  const handleOnBlur = (event) => {
    const selectedTime = moment();
    if (clearable && !event.currentTarget.contains(event.relatedTarget)) {
      if (showHours && hours) selectedTime.hours(hours);
      if (showMinutes && minutes) selectedTime.minutes(minutes);
      if (showSeconds && seconds) selectedTime.seconds(seconds);
      if (use12Hours && meridiem) {
        const nextHours =
          meridiem.toLowerCase() === AM ? hours % 12 : (hours % 12) + 12;
        selectedTime.hour(nextHours);
      }
      onChange(selectedTime);
    }
  };

  const renderSecondsInput = () => {
    if (!showSeconds) return null;

    return (
      <TimeInput
        {...commonInputProps(
          (e) => onKeyDown(e, GENERAL_TIME_RANGE.max),
          onInputFocus,
          onClick,
        )}
        key="seconds-input"
        data-testid="ds-time-input__seconds"
        aria-label={ariaLabel || ariaLabel2 || 'Seconds Input'}
        disabled={disabled}
        // eslint-disable-next-line no-return-assign
        innerRef={(node) => (inputRefs.seconds = node)}
        onChange={handleSecondsChange}
        placeholder={placeholderFormat(PLACEHOLDER_TIME.seconds, format)}
        value={seconds}
      />
    );
  };

  const renderMinutesInput = () => {
    if (!showMinutes) return null;

    return (
      <TimeInput
        {...commonInputProps(
          (e) => onKeyDown(e, GENERAL_TIME_RANGE.max),
          onInputFocus,
          onClick,
        )}
        key="minutes-input"
        data-testid="ds-time-input__minutes"
        aria-label={ariaLabel || ariaLabel2 || 'Minutes Input'}
        disabled={disabled}
        // eslint-disable-next-line no-return-assign
        innerRef={(node) => (inputRefs.minutes = node)}
        onChange={handleMinutesChange}
        placeholder={placeholderFormat(PLACEHOLDER_TIME.minutes, format)}
        value={minutes}
      />
    );
  };

  const renderHoursInput = () => {
    if (!showHours) return null;
    const hourFormatRange = use12Hours ? HOUR_RANGE_12 : HOUR_RANGE_24;

    return (
      <TimeInput
        {...commonInputProps(
          (e) => onKeyDown(e, hourFormatRange.max),
          onInputFocus,
          onClick,
        )}
        key="hour-input"
        data-testid="ds-time-input__hours"
        aria-label={ariaLabel || ariaLabel2 || 'Hours Input'}
        disabled={disabled}
        // eslint-disable-next-line no-return-assign
        innerRef={(node) => (inputRefs.hours = node)}
        onChange={handleHoursChange}
        placeholder={placeholderFormat(
          PLACEHOLDER_TIME.hour,
          format,
          use12Hours,
        )}
        value={hours}
      />
    );
  };

  const renderMeridiemInput = () => {
    if (!use12Hours) return null;

    return (
      <TimeInput
        {...commonInputProps}
        key="meridiem-input"
        data-testid="ds-time-input__ampm"
        aria-label={ariaLabel || ariaLabel2 || 'Meridiem Input'}
        disabled={disabled}
        onBlur={handleMeridiemBlur}
        onKeyDown={handleMeridiemKeyDown}
        placeholder={placeholderFormat(PLACEHOLDER_TIME.meridiem, format)}
        value={meridiem}
      />
    );
  };

  const renderTimeInputs = () => {
    const inputs = [
      renderHoursInput(),
      renderMinutesInput(),
      renderSecondsInput(),
    ];
    /* eslint-disable indent */
    const inputsWithDividers = inputs.reduce(
      (acc, input, index) =>
        input
          ? acc.concat([
              index ? (
                <TimeDivider
                  // eslint-disable-next-line react/no-array-index-key
                  key={index}
                  content=":"
                  value={input.props.value}
                />
              ) : null,
              input,
            ])
          : acc,
      [],
    );
    /* eslint-enable indent */

    return [
      inputsWithDividers,
      React.createElement('span', { key: 'span' }), // hack for next/prev focus
      renderMeridiemInput(),
    ];
  };

  return (
    <InputTimesGroup onBlur={handleOnBlur} innerRef={innerRef}>
      {renderTimeInputs()}
    </InputTimesGroup>
  );
};

export default TimeInputs;
